@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@800&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: 'Mulish', sans-serif;
}

.navbar {
	background-color: #fff !important;
}

.navbar-brand {
	font-size: 1.9rem !important;
	color: #565387 !important;
}

.navbar a {
	font-size: 1.3rem;
	text-transform: capitalize;
}

.menu_active {
	font-weight: bold;
	border-bottom: 1px solid #565387;
}

.navbar a:hover {
	color: #3498db !important;
}

@media (max-width: 991px) {
	.navbar-brand {
		font-size: 1.3rem !important;
	}

	.menu_active {
		font-weight: bold;
		border-bottom: none;
	}
}

/* maing landing section */

#header {
	width: 100%;
	height: 80vh;
	margin-top: 10px;
}

#header h1 {
	margin: 0 0 10px 0;
	font-size: 3rem;
	font-weight: 700;
	color: black;
}

#header .brand_name {
	color: #565387;
}

#header p {
	color: #484848;
	font-size: 1rem;
}

#header .animated {
	animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
	0% {
		transform: translateY(10px);
	}
	100% {
		transform: translateY(-10px);
	}
}

@keyframes up-down {
	0% {
		transform: translateY(10px);
	}
	100% {
		transform: translateY(-10px);
	}
}

@media (max-width: 668px) {
	#header h1 {
		font-size: 2.5rem;
	}
}

footer p {
	padding: 0;
	margin: 0;
}

@media (max-width: 991px) {
	#header {
		height: 80vh;
		text-align: center;
	}
	#header .header_img {
		text-align: center;
	}
	#header .header_img img {
		width: 60%;
	}
}

@media (max-width: 768px) {
	#header {
		margin-top: 20px;
	}

	#header h1 {
		font-size: 28px;
	}
	#header p {
		font-size: 18px;
		margin-bottom: 30px;
	}

	#header .header_img img {
		width: 70%;
	}
}

